body{
    text-align: center;
    font-size: 20px;
    padding: 2.5%;
}

.my_profile{
    height: 180px;
    width: 160px;
    border: 5px white solid;
    border-radius: 50%;
    -webkit-transition: border-radius 0.3s ease-in-out;
}
.my_profile:hover{
    box-shadow: 5px 5px 5px 0 rgba(0,0,0,0.08);
    border: 5px white groove;
    border-radius: 0%;
    transition: box-shadow 0.5s ease-in-out;
    -webkit-transition: border-radius 0.5s ease-in-out;
}